body {
  background-color: gray;
  font-style: normal;
  font-weight: 500;
}

.container {
  width: 1024px;
  margin: 0 auto;
}

h1, h2 {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

.header {
  background-image: url("header-bg.e930ef5d.png");
  background-repeat: no-repeat;
  background-size: cover;
  flex-direction: column;
  margin: 0 auto;
  padding: 40px 70px 97px;
  display: flex;
}

.header h1 {
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 30px;
  line-height: 1.167;
}

.header__title_wrapper {
  align-items: center;
  display: flex;
}

.header__title_wrapper img {
  margin-right: 10px;
}

.header-top {
  justify-content: space-between;
  margin-bottom: 37px;
  display: flex;
}

.header-top ul {
  list-style: none;
  display: flex;
}

.header-top ul li:first-child {
  margin-right: 40px;
}

.header-top ul a {
  text-transform: uppercase;
  color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.167;
}

.main {
  background-color: #fff;
  padding: 60px 70px;
}

.loader {
  width: 120px;
  height: 120px;
  border: 16px solid #f3f3f3;
  border-top-color: #3498db;
  border-radius: 50%;
  margin: 50px auto;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

